import React from "react";
import { Link } from "react-router-dom";
import "../styles/AuthorModal.css";


const AuthorModal = ({ visiable, onClose, author }) => {
    if (!visiable) return null;
    
    return (
        <div className="author-modal-overlay" onClick={onClose}>
            <div 
                className="author-modal-content" 
                onClick={(e) => e.stopPropagation()} 
            >
                <button 
                    className="close-button" 
                    onClick={onClose}
                >
                    &times;
                </button>
                <h2>  </h2>
                {author ? (
                    <div>
                        <Link to={`/profile/${author.id_organization}`} style={{ textDecoration: "none" }}>
                            <div>
                            {author.prof_photo 
                                ?
                                <img src={`${author.prof_photo}`} alt="" style={{maxWidth: "20vw"}}/>
                                : null
                            }
                            </div>
                        </Link>
                        <Link to={`/profile/${author.id_organization}`} style={{ textDecoration: "none" }}>
                            <p><strong>:</strong> {author.FIO ? author.FIO : author.name_organization}</p>
                        </Link>
                        <p><strong> :</strong> {author.email}</p>
                        {author.phone_1
                            ?
                            <p><strong>:</strong> {author.phone_1}</p>
                            : null
                        }
                        {author.phone_2
                            ?
                            <p><strong> :</strong> {author.phone_2}</p>
                            : null
                        }
                    </div>
                ) : (
                    <p>   ...</p>
                )}
            </div>
        </div>
    );
};

export default AuthorModal;
